import React, {useState, useContext} from 'react'
import {Avatar, Button, TextField, FormControlLabel, Checkbox, Link, Paper, Box, Grid, Typography, Dialog, DialogTitle, DialogActions, DialogContent, DialogContentText} from '@material-ui/core'
import HeadsetIcon from '@material-ui/icons/Headset'
import {makeStyles} from '@material-ui/core/styles'
import {useHistory} from 'react-router-dom'
import ProfilePage from './ProfilePage'
import {AuthContext} from '../contexts/AuthContext'

function Copyright() {
  return (
      <Typography variant='body2' color='textSecondary' align='center'>
          {'Copyright © '}
          <Link color='inherit' href='https://material-ui.com/'>
              BeatHouse by Leandro Lopez
          </Link>{' '}
          {new Date().getFullYear()}
          {'.'}
      </Typography>
  )
}

const useStyles = makeStyles((theme) => ({
    root: {
        height: '100vh',
    },
    image: {
        backgroundImage: 'url(https://source.unsplash.com/random)',
        backgroundRepeat: 'no-repeat',
        backgroundColor:
        theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
        backgroundSize: 'cover',
        backgroundPosition: 'center',
    },
    paper: {
        margin: theme.spacing(8, 4),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '80%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}))

const SignInPage = () => {
    const classes = useStyles()
    const [user, setUser] = useState('')
    const [password, setPassword] = useState('')
    const [open, setOpen] = useState(false)
    const history = useHistory()
    const {logged} = useContext(AuthContext)

    const signIn = () => {
        const requestOptions = {
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            credentials: 'include',
            body: JSON.stringify({username: user, password: password})
        }
        fetch('/session/login', requestOptions)
        .then(response => response.json())
        .then(data => {
            history.push('/')
        })
        .catch(err =>{
            if(!open)setOpen(true)
            console.log("ERRROR" , err)
        })
    }

    const handleClose = () => {
        setUser('')
        setPassword('')
        setOpen(false)
    }

    if(logged) return <ProfilePage/>

    return (
        <Grid container component='main' className={classes.root}>
            <Grid item xs={false} sm={4} md={7} className={classes.image}/>
            <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
                <div className={classes.paper}>
                    <Avatar className={classes.avatar}>
                        <HeadsetIcon/>
                    </Avatar>
                    <Typography component='h1' variant='h5'>
                        Sign in
                    </Typography>
                    <form className={classes.form} onSubmit={e => e.preventDefault()}>
                        <TextField
                            onInput={(event) => setUser(event.target.value)}
                            variant='outlined'
                            margin='normal'
                            inputProps={{maxLength: 50}}
                            required
                            fullWidth
                            value={user}
                            label='Username'
                            name='username'
                            autoComplete='username'
                            autoFocus
                        />
                        <TextField
                            onInput={(event) => setPassword(event.target.value)}
                            variant='outlined'
                            margin='normal'
                            required
                            value={password}
                            fullWidth
                            label='Password'
                            name='password'
                            type='password'
                            autoComplete='current-password'
                        />
                        <FormControlLabel
                            control={<Checkbox value='remember' color='primary'/>}
                            label='Remember me'
                        />
                        <Button
                            onClick={signIn}
                            type='submit'
                            fullWidth
                            variant='contained'
                            color='primary'
                            className={classes.submit}
                        >
                            Sign In
                        </Button>
                        <Grid container>
                            <Grid item>
                                <Link href='/SignUp' variant='body2'>
                                    {"Don't have an account? Sign Up"}
                                </Link>
                            </Grid>
                        </Grid>
                    </form>
                    <Box mt={5}>
                        <Copyright />
                    </Box>
                </div>
            </Grid>
            {open &&(
                <Dialog
                    open={open}
                    onClose={handleClose}
                >                              
                    <DialogTitle>{'Sign in Error'}</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            Invalid Username or Password
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} color='primary'>
                            Try again
                        </Button>
                    </DialogActions>
                </Dialog>
            )}
        </Grid>
    )
}
export default SignInPage